import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {ReactiveFormsModule , FormsModule} from '@angular/forms';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { VldashboardComponent } from './vldashboard/vldashboard.component';
import { SavevideoComponent } from './savevideo/savevideo.component';
import {YouTubePlayerModule} from '@angular/youtube-player';
import { NgxBootstrapIconsModule, allIcons } from 'ngx-bootstrap-icons';
import { VideoComponent } from './video/video.component';
import { EditvideoComponent } from './editvideo/editvideo.component';
import { Ng2SearchPipeModule } from 'ng2-search-filter';

const routes: Routes = [{path: '', component: LoginComponent},
{path: 'dashboard', component: VldashboardComponent},
{path: 'savevideo', component: SavevideoComponent},
{path: 'video/:id', component: VideoComponent},
{path: 'editvideo/:ytid', component: EditvideoComponent}];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    VldashboardComponent,
    SavevideoComponent,
    VideoComponent,
    EditvideoComponent
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    FormsModule,
    HttpClientModule,
    Ng2SearchPipeModule,
    YouTubePlayerModule,
    NgxBootstrapIconsModule.pick(allIcons),
    RouterModule.forRoot(routes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
