import { VideoService } from './../video.service';
import { LoggingService } from './../logging.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';



@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit, OnDestroy {

  url: any;
  ytid = '';
  YTid = '';
  error = null;

  constructor(private route: Router, private ls: LoggingService , private activeroute: ActivatedRoute , private vs: VideoService
  ,           private sanitizer: DomSanitizer) {}
  ngOnInit(): void {
    this.activeroute.params.subscribe( params => {
      this.ytid = params.id;
      this.YTid = this.ytid;
    });
    this.url = 'http://www.youtube.com/embed/' + this.YTid + '?enablejsapi=1&origin=http://localhost:4200';
  }


  getSantizeUrl(url: string): any {
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  goback(): any{
    this.route.navigate(['/dashboard']);
  }

  delete(id: any): any{
    console.log(id);
    this.vs.deleteVideo(id).subscribe(res => {
      console.log(res);
      this.route.navigate(['/dashboard']);
    }, err => {
      if (err.error.message === 'jwt expired'){
        this.logout();
      }
      else{
        this.error = err.error.message;
      }
      console.log(err);
    });
  }

  logout(): any{
    this.ls.logout();
  }

  ngOnDestroy(): void {
}
}
