import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { VideoService } from '../video.service';
import { LoggingService } from '../logging.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-editvideo',
  templateUrl: './editvideo.component.html',
  styleUrls: ['./editvideo.component.css']
})
export class EditvideoComponent implements OnInit {

  constructor(private route: Router , private vs: VideoService , private ls: LoggingService , private act: ActivatedRoute) { }
  videoform: FormGroup;
  error = null;
  id = null;
  YTid = '';
  ngOnInit(): void {
    this.videoform = new FormGroup({
      title: new FormControl(null),
      desc: new FormControl(null)
    });
    this.act.params.subscribe( params => {
      this.id = params.ytid;
      this.YTid = this.id;
    });
  }

  editvideo(): any{
    console.log(this.id);
    this.vs.editvideo({title: this.videoform.value.title, id: this.id,
       desc: this.videoform.value.desc , email: localStorage.getItem('email')}).
    subscribe( res => {
      console.log(res);
      this.route.navigate(['/dashboard']);
    }, err => {
      if (err.error.message === 'jwt expired'){
        this.logout();
      }
      else{
        this.error = err.error.message;
      }
      this.error = err.error.message;
      console.log(err);
    });
  }

  GoBack(): any{
    this.route.navigate(['/dashboard']);
  }

  logout(): any{
    this.ls.logout();
  }

  clearError(): any{
    this.error = null;
  }

}
