import { LoggingService } from './../logging.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators} from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginpage = true;
  loginform: FormGroup;
  registerform: FormGroup;
  error = null;
  private expirationDuration: any;
  constructor(private route: Router, private ls: LoggingService) { }

  ngOnInit(): void {

    if (localStorage.getItem('email') && localStorage.getItem('token'))
    {
      this.route.navigate(['/dashboard']);
    }
    this.loginform = new FormGroup({
      Email : new FormControl(null, [Validators.required , Validators.email]),
      pass : new FormControl(null, [Validators.required])
    });
    this.registerform = new FormGroup({
      username : new FormControl(null , [Validators.required, Validators.minLength(5) , Validators.maxLength(20)]),
      email : new FormControl(null, [Validators.required , Validators.email]),
      password : new FormControl(null, [Validators.required, Validators.minLength(6) , Validators.maxLength(10)]),
      cpassword : new FormControl(null, [Validators.required, Validators.minLength(6) , Validators.maxLength(10)])
    });
  }

  login(): any{
    this.ls.login({email: this.loginform.value.Email, password: this.loginform.value.pass}).
    subscribe( res => {
      console.log(res);
      this.loginform.reset();
      localStorage.setItem('token', res.body.token);
      localStorage.setItem('email', res.body.email);
      localStorage.setItem('expiry', res.body.expiry);
      this.route.navigate(['/dashboard']);
      this.expirationDuration = res.body.expiry * 60 * 1000 * 1000 * 60;
    }, err => {
      this.error = err.error.message;
      console.log(err);
    });
  }

  changepage(): void{
    this.loginpage = !this.loginpage;
  }
  clearError(): any{
    this.error = null;
  }

  register(): any{
    if (this.registerform.value.password !== this.registerform.value.cpassword)
    {
      this.error = 'conform password should match password';
    }
    else
    {
      this.ls.register({email: this.registerform.value.email, username: this.registerform.value.username,
        password: this.registerform.value.password, cpassword: this.registerform.value.password})
     .subscribe( res => {
       console.log(res);
       this.registerform.reset();
       localStorage.setItem('token', res.body.token);
       localStorage.setItem('email', res.body.email);
       localStorage.setItem('expiry', res.body.expiry);
       this.route.navigate(['/dashboard']);
     }, err => {
       this.error = err.error.message;
       console.log(err);
     });
    }
  }

}
