import { DomSanitizer } from '@angular/platform-browser';
import { LoggingService } from './../logging.service';
import { VideoService } from './../video.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import * as io from 'socket.io-client';


@Component({
  selector: 'app-vldashboard',
  templateUrl: './vldashboard.component.html',
  styleUrls: ['./vldashboard.component.css']
})
export class VldashboardComponent implements OnInit {

  videos = [];
  len = 0;
  error = null;
  socket: SocketIOClient.Socket;
  token = null;
  loading = null;
  searchText;
  constructor(private router: Router, private vs: VideoService , private ls: LoggingService , private sanitizer: DomSanitizer) {
    this.socket = io.connect();
    this.getAllVideos();
  }

  ngOnInit(): void {
    this.token = localStorage.getItem('token');
    this.socket.on('posts', data => {
      if ( data.userid === localStorage.getItem('email')){
        if (data.action === 'Deleted Video' || data.action === 'UPDATE Video' || data.action === 'SAVE Video'){
        this.videos = [];
        this.getAllVideos();
        }
      }
    });
  }

  getAllVideos(): any {
    this.videos = [];
    this.loading = true;
    this.vs.getvideos().subscribe( res => {
      Object.keys(res.body).forEach(ele => {
        this.videos.push(res.body[ele]);
      });
      this.loading = false;
      this.len = this.videos.length;
      console.log(this.videos);
    }, err => {
      console.log(err , 'error');
      this.loading = false;
      if (err.error.message === 'jwt expired'){
        this.logout();
      }
      else{
        this.error = err.error.message;
      }
    });
  }

  getSantizeUrl(url: string): any {
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  edit(id: any , ytid: any): any{
    this.router.navigate(['/editvideo/' + ytid]);
  }

  addvideo(): any{
    this.router.navigate(['/savevideo']);
  }
  play(data: any): any{
    this.router.navigate(['/video/' + data]);
  }

  delete(id: any): any{
    console.log(id);
    this.loading = true;
    this.vs.deleteVideo(id).subscribe(res => {
      this.loading = false;
      console.log(res);
    }, err => {
      console.log(err);
      this.loading = false;
      if (err.error.message === 'jwt expired'){
        this.logout();
      }
      else{
        this.error = err.error.message;
      }
    });
  }
  logout(): any{
    this.ls.logout();
  }

  deleteAll(): any
  {
    this.loading = true;
    this.vs.deletAll().subscribe( res => {
      console.log(res);
      this.loading = false;
    }, err => {
      this.loading = false;
      if (err.error.message === 'jwt expired'){
        this.logout();
      }
      else{
        this.error = err.error.message;
      }
      console.log(err);
    });
  }

  clearError(): any{
    this.error = null;
  }
}
