import { Router } from '@angular/router';
import { LoggingService } from './logging.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  title = 'videolibrary';
  loginstatus = null;
  token = localStorage.getItem('vl');
  constructor(private Loginserve: LoggingService , private route: Router){
    this.Loginserve.loginstatus.subscribe( res => {
      this.loginstatus = res;
    });
  }
  ngOnInit(): void{
    this.Loginserve.loginstatus.subscribe( res => {
      this.loginstatus = res;
      console.log(this.loginstatus, 'res');
    });
  }
  logout(): any
  {
    this.loginstatus = false;
    this.Loginserve.loginstatus.emit(false);
    localStorage.removeItem('vl');
    this.token = null;
    this.route.navigate(['/']);
  }

  addvideo(): any{
    this.route.navigate(['/savevideo']);
  }
}
