import { LoggingService } from './../logging.service';
import { VideoService } from './../video.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-savevideo',
  templateUrl: './savevideo.component.html',
  styleUrls: ['./savevideo.component.css']
})
export class SavevideoComponent implements OnInit {

  constructor(private route: Router , private vs: VideoService , private ls: LoggingService) { }
  videoform: FormGroup;
  error = null;
  Url = '';
  ngOnInit(): void {
    this.videoform = new FormGroup({
      title: new FormControl(null , [Validators.required, Validators.minLength(4)]),
      url: new FormControl(null, [Validators.required]),
      desc: new FormControl(null, [Validators.required, Validators.minLength(4)])
    });
  }

  savevideo(): any{
    this.vs.savevideo({title: this.videoform.value.title, url: this.videoform.value.url,
       desc: this.videoform.value.desc , email: localStorage.getItem('email')}).
    subscribe( res => {
      console.log(res);
      this.route.navigate(['/dashboard']);
    }, err => {
      if (err.error.message === 'jwt expired'){
        this.logout();
      }
      else{
        this.error = err.error.message;
      }
      console.log(err);
    });
  }

  GoBack(): any{
    this.route.navigate(['/dashboard']);
  }

  logout(): any{
    this.ls.logout();
  }

  clearError(): any{
    this.error = null;
  }
}
